/* General Page Styling */
body {
    font-family: "Poppins", sans-serif;
    background: #e4e9f7;
    margin: 0;
    padding: 0;
}

/* Form Styling */
h2 {
    color: #333;
    margin-bottom: 20px;
}

input[type="text"] {
    width: 250px;
    padding: 8px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
}

button {
    padding: 8px 15px;
    background: #4169E1;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: background 0.3s ease;
}

button:hover {
    background: #364fc7;
}

/* Table Styling */
table {
    width: 100%;
    max-width: 800px;
    margin: 20px 0;
    border-collapse: collapse;
    background: white;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    overflow: hidden;
}

th, td {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: left;
}

th {
    background: #4169E1;
    color: white;
    font-weight: bold;
}

tr:nth-child(even) {
    background: #f2f2f2;
}

tr:hover {
    background: #d1d8e0;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
    input[type="text"], button {
        width: 100%;
        margin-bottom: 10px;
    }

    table {
        width: 100%;
        overflow-x: auto;
        display: block;
    }

    th, td {
        padding: 10px;
        font-size: 14px;
    }
}

/* Dropdown container */
.dropdown {
    position: relative;
    display: inline-block;
}

/* Button that triggers the dropdown */
.dropbtn {
    background-color: transparent;
    color: black;
    border: none;
    font-size: 18px;
    cursor: pointer;
    padding: 5px;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 100px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1;
    border-radius: 5px;
    overflow: hidden;
}

/* Style for each dropdown option */
.dropdown-content a {
    color: black;
    padding: 8px 12px;
    display: block;
    text-decoration: none;
    font-size: 14px;
}

/* Show the dropdown when hovered */
.dropdown:hover .dropdown-content {
    display: block;
}

/* Hover effect for options */
.dropdown-content a:hover {
    background-color: #f1f1f1;
}

/* Modal styling */
.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

/* Modal content */
.modal-content {
    background-color: white;
    margin: 10% auto;
    padding: 20px;
    width: 300px;
    border-radius: 8px;
    text-align: center;
}

/* Close button */
.close {
    float: right;
    font-size: 24px;
    cursor: pointer;
}

/* Input fields */
input {
    width: 100%;
    padding: 8px;
    margin: 5px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
}
